import { rad } from '../utils/utils'

/**
 *
 * @param circleWidth 반지름
 * @param x 중심의 X 좌표
 * @param y 중심의 Y 좌표
 * @param startRadius 원의 시작 각도
 * @param endRadius 원의 마지막 각도
 * @returns WallDef
 */
function circleGenerator(
  circleWidth: number,
  x: number,
  y: number,
  startRadius: number,
  endRadius: number
) {
  let wall: WallDef[] = []
  for (var i: number = startRadius; i < endRadius; i++) {
    wall.push([
      Math.cos((Math.PI / 180) * i) * circleWidth + x,
      Math.sin((Math.PI / 180) * i) * circleWidth + y,
    ])
  }
  return wall
}

type WallDef = [x: number, y: number]
type BoxDef = [
  x: number,
  y: number,
  angle: number,
  width: number,
  height: number
]
type WheelDef = [
  x: number,
  y: number,
  power: number,
  centerX?: number,
  centerY?: number,
  size?: number
]

type JumperDef = [x: number, y: number, size: number, temporary?: boolean]

type StageDef = {
  walls: WallDef[][]
  boxes: BoxDef[]
  wheels: WheelDef[]
  jumpers?: JumperDef[]
  goalY: number
  zoomY: number
}

